
import cTitle from 'components/title';
import { MessageBox } from 'mint-ui';
import { Lazyload, Button } from 'mint-ui';
export default {
    data() {
        return {
            order_id: '',
            order_data: '',
            bottomStatus: '',
            selected: '1',
            allLoaded: false,
            isVirtual: false,
            receiver: '',
            mobile: '',
            address: ''
        };
    },
    methods:
    {
        operation(btn, order) {

            //this.actions=[{name:"无理由",method:"sheetAction"},{name:"不退了",method:"sheetAction"}];
            if (btn.value == 8) {
                this.checklogistics(order, btn);
            } else if (btn.value == 12) {
                this.deleteOrder(order, btn);

            } else if (btn.value == 10) {
                this.comment(order, btn);

            } else if (btn.value == 1) {
                this.toPay(order, btn);
            } else if (btn.value == 13) {
                //退款
                this.toRefund(order, btn);

            } else if (btn.value == 9) {
                //取消订单
                this.cancleOrder(order, btn);

            } else if (btn.value == 5) {
                //确认订单
                this.confirmOrder(order, btn);

            }

        },
        checklogistics(item, btn) {
            this.$router.push({ name: "logistics", params: { order: item } });

        },
        cancleOrder(item, btn) {
            MessageBox({
                title: '提示',
                message: '确定取消此订单?',
                showCancelButton: true
            }).then(action => {
                if (action == 'confirm') {
                    //删除当前选中的地址
                    // this.datasource.splice(this.datasource.indexOf(item), 1);
                    this.sentRequest(btn.api, { order_id: item.id }, false);

                }

            });

        },
        //删除订单
        deleteOrder(item, btn) {
            MessageBox({
                title: '提示',
                message: '确定删除此订单?',
                showCancelButton: true
            }).then(action => {
                if (action == 'confirm') {
                    //删除当前选中的地址
                    this.sentRequest(btn.api, { order_id: item.id }, false);

                }

            });


        },
        toPay(item, btn) {

            this.$router.push({ name: "orderpay", params: { order: item } });

        },
        //评价订单
        comment(item, btn) {

            this.$router.push({ name: "comment", params: { order: item } });

        },
        confirmOrder(item, btn) {
            MessageBox({
                title: '提示',
                message: '您确定收到货物?',
                showCancelButton: true
            }).then(action => {
                if (action == 'confirm') {
                    var that = this;
                    //this.$emit('ConfrimOrderNotification', item);
                    //调用接口通知服务器收到货物请求
                    $http.get(btn.api, { order_id: item.id }, '正在处理订单...').then(function (response) {
                        if (response.result == 1) {
                            that.$router.go(-1);

                        } else {
                            MessageBox(response.msg, '确定');
                            that.$router.go(-1);
                        }


                    }, function (response) {
                        // error callback
                    });

                } else {

                }

            });


        },
        //追加评价
        reCommend(item, btn) {

        },
        //退款
        toRefund(item, btn) {
            //refund
            this.$router.push({ name: "refund", params: { order: item } });

        },
        sentRequest(action, params, isIndicator) {
            var that = this;
            $http.get(action, params, '').then(function (response) {

                if (response.result == 1) {
                    MessageBox.alert(response.msg, '提示');
                    that.$router.go(-1);
                } else {
                    MessageBox.alert(response.msg, '提示');

                }
            }, function (response) {
                // error callback
            });
        },
        //发起http请求
        getNetData(order_id) {
            var that = this;
            $http.get('order.detail', { order_id: order_id }, '正在获取订单...').then(function (response) {

                if (response.result == 1) {
                    var myData = response.data;
                    console.log(response.data);
                    that.order_data = myData;
                    that.address = response.address_info.address;
                    that.mobile = response.address_info.mobile;
                    that.receiver = response.address_info.realname;


                } else {
                    alert(response.msg);
                    that.$router.go(-1);
                }
            }, function (response) {
                // error callback
            });

        }


    },

    activated() {

        //console.log( this.$store.state);
        this.order_id = this.$route.params.order_id;
        this.getNetData(this.order_id);
        console.log(this.order_id);
        //   console.log(this.order_data);
        //this.isVirtual  = this.order_data.isVirtual;


    },
    components: { cTitle }
}


