<template v-title="订单详情">
    
    <div id="goodsinfo">
    	<c-title :hide="false" text='订单详情'></c-title>
    	<div style="height: 45px;"></div>
    	<div class="addr">
    		<i class="fa fa-map-marker"></i>
    		<p>收件人：{{receiver}}（{{mobile}}）<br />
    			<span>{{address}}</span>
    		</p>
    	</div>
    	<div class="detail_good">
    		<h3><i class="fa fa-gift"></i></h3>
            <div class="goods" v-for="good in order_data.has_many_order_goods">
                <div class="img"><img  v-lazy="good.thumb"></div>
                <div class="inner">
                    <div class="name">{{good.title}}</div>     
                    <div class="option">规格:  {{good.goods_option_title}}</div>
                </div>
                <div class="price">
                    ￥{{good.goods_price}}<br/>   ×{{good.total}}
                </div>
            </div>
    	</div>
    	<div class="detail_good" v-if="0">
    		<h3><i class="fa fa-truck"></i>发货信息</h3>
    		<div class="content" >
    			1、激活码：553321  中国移动：135 911 234 56  链接：<a href="#">点攻击链接</a><br />
    			2、激活码：967454  中国移动：137 334 234 56  链接：<a href="#">点攻击链接</a><br />
    		</div>
    	</div>	
    	<div class="tbs">
			<div class="left">订单编号:</div>
			<div class="right">{{order_data.order_sn}}</div>
			<div class="left">交易状态:</div>
			<div class="right">{{order_data.status_name}}</div>			
			<div class="left">商品小计:</div>
			<div class="right">￥{{order_data.goods_price}}</div>
			<template  v-for="info in order_data.discount_details">
				<div class="left">{{info.name}}:</div>
			    <div class="right">￥{{info.price}}</div>
			</template>
			<div class="left">总抵扣:</div>
			<div class="right">￥{{order_data.discount_price}}</div>
			<div class="left">运费:</div>
			<div class="right">￥{{order_data.dispatch_price}}</div>
			<div class="left">实付费(含运费):</div>
			<div class="right">￥{{order_data.price}}</div>			
    	</div>
    	<div style="height: 50px;"></div>
		<div class="detail_pay">
		    <div class="order_delete" v-for="(item,index) in order_data.button_models" @click="operation(item,order_data)" >{{item.name}}</div>
		</div>    	
    </div>
   
</template>
<script>
import order_detailcontroller from './order_detailcontroller';
export default order_detailcontroller;

</script>
<style lang="scss" rel="stylesheet/scss" scoped>
#goodsinfo{
	.addr{display: flex;flex-flow: row wrap;align-items:center;background: #FFF; border-bottom: #d8c7c7 1px solid; margin-bottom: 10px;
		i{flex: 1;font-size: 20px;}
		p{flex: 9;text-align: left; span{color: #858585;font-size: .6rem;}}
	}
	.detail_good{background: #FFF;padding: 0 10px;overflow: hidden;
		.content{text-align: left;background: #efeded;padding: 10px;color: #8C6700;}
		a{color: #000000;}
		h3{text-align: left;margin: 9px 0; i{font-size:20px;padding-right: 5px;}}
		.goods{display: flex;align-items: stretch;  flex-flow: row wrap;background: #efeded;padding: 5px;margin-bottom: 10px;
			.img{flex:3; img{width: 100%;}}
			.inner{flex: 5;padding: 0 5px;display: flex;flex-direction:column;align-items: flex-start;}
			.name{flex: 5; text-align: left}
			.price{flex: 2;align-items:center;}
			.option{color: #888;font-size: .6rem;flex: 1;}
			}
		}
	.tbs{ display: flex;align-items:center; background: #FFF; flex-flow: row wrap; padding:10px; line-height: 1.5rem;
		.left{text-align: left;flex: 40%;color:#858585;}
		.right{text-align: right;flex: 60%;}
	}
.detail_pay { height: 3rem; width: 100%; background: #fff; padding: 0px 2%; margin-top: 30px; border-top: 1px solid #eaeaea;
    position: fixed; bottom: 0px; box-sizing: border-box;
    .order_delete {height: 1.5rem; width: auto; margin-left: 5px; background: #fff; padding: 0px 10px; margin-top: 10px; border-radius: 5px; color: #5f6e8b;
    line-height: 1.5rem; float: right; border: 1px solid #5f6e8b;}
    }	
}
</style>


